import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useState } from 'react';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { toast } from 'sonner';
import TextAreaInputForm from '@/components/forms/TextAreaInputForm';
import SelectInputForm from '@/components/forms/SelectInputForm';
import { DateInputForm } from '@/components/forms/DateInputForm';
import Cookies from 'js-cookie';
import { format } from 'date-fns';
import { AddLoanRequestFormValues, AddLoanRequestSchema } from '../schemas/insurances/add-loan-request-schema';
import TextInputForm from '@/components/forms/TextInputForm';

interface Props {
    setShowPopup: (value: boolean) => void;
}

export default function LoanRequestForm({ setShowPopup }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const employee_id = JSON.parse(Cookies.get('user') || '{}').id;

    const form = useForm<AddLoanRequestFormValues>({
        resolver: zodResolver(AddLoanRequestSchema),
    });

    const onSubmit = async (data: AddLoanRequestFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors
        const formatedData = {
            ...data,
            employee_id,
            data: format(data.date, 'yyyy-MM-dd'),
        };
        try {
            await apiPost({
                endpoint: endpoints.loan.create,
                body: formatedData,
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            toast('تم اضافة الطلب بنجاح');
            setShowPopup(false);
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mt-6">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <div className="grid grid-cols-2 gap-x-6">
                        <SelectInputForm
                            name="loan_type"
                            label="نوع القرض"
                            options={[
                                { value: 'sick', name: 'قرض طارئ' },
                                { value: 'annual', name: 'قرض سنوي' },
                                { value: 'religious', name: 'قرض ادياني' },
                                { value: 'other', name: 'قرض أخرى' },
                            ]}
                            placeholder="اختر نوع القرض"
                        />
                        <TextInputForm name="amount" label="القيمة" placeholder="100" type="number" />
                        <DateInputForm name="date" label="التاريخ" placeholder="2004/04/30" />
                        <TextInputForm name="installments" label="المدة" placeholder="2004/04/30" />
                    </div>
                    <TextAreaInputForm
                        className="flex flex-col min-h-[157px]"
                        name="reason"
                        label="السبب"
                        placeholder="اكتب السبب"
                    />
                    <div className="flex flex-row gap-4 mt-12">
                        <SecondaryButton
                            onClick={() => setShowPopup(false)}
                            type="button"
                            className="w-full text-base leading-5 tracking-normal py-[10px] font-medium"
                        >
                            إلغاء
                        </SecondaryButton>
                        <PrimaryButton
                            type="submit"
                            className="w-full text-base leading-5 tracking-normal font-medium py-[10px]"
                        >
                            {loading ? 'جاري الحفظ...' : 'تقديم الطلب'}
                        </PrimaryButton>
                    </div>
                </form>
            </Form>
        </>
    );
}
