import { z } from 'zod';

export const AddLoanRequestSchema = z.object({
    date: z.date(),
    installments: z.string().min(1, 'Installments field is required.'),
    loan_type: z.string().min(1, 'Loan type field is required.'),
    amount: z.string().min(1, 'Amount field is required.'),
});

export type AddLoanRequestFormValues = z.infer<typeof AddLoanRequestSchema>;
